/* eslint-disable no-new */
<template>
  <div class="artists">
    <b-container>
      <!--HeadText--->
      <b-row>
        <b-col>
          <b-card class="mb-3">
            <b-card-text>
              SCHALLINTERFERENZ ist bekannt fr die extravagante Auswahl an
              Artists bekannt. Jeder fr sich pflegt einen markanten Stil. Doch
              von allen Stilrichtungen die hier vertreten sind, spielt
              HardTechno eine treibende Rolle. Falls sie interessiert an der
              Vita des einzelnen Artists, oder an einem Booking des Knstlers,
              klicken sie auf "BUCHEN"
            </b-card-text>
          </b-card>
        </b-col>
      </b-row>

      <!--Middle--->
      <!----->

      <!--Artist Pictures--->
      <b-row style="padding-bottom: 10px">
        <b-col>
          <b-card>
            <h1><b>LABELARTISTS</b></h1>
          </b-card>
        </b-col>
      </b-row>

      <b-row>
        <div v-for="obj in arDat" v-bind:key="obj.aId">
          <div v-if="obj.type === 'l'">
            <b-col cols="12">
              <b-card img-top>
                <b-card-header>
                  <h2>
                    <b>{{ obj.name }}</b>
                  </h2>
                </b-card-header>

                <b-img
                  thumbnail
                  fluid
                  :src="obj.img"
                  width="313px"
                  height="300px"
                >
                </b-img>

                <b-card-title>
                  <h2>
                    <i>MAIN LABEL: </i><b>{{ obj.mainLabel }}</b>
                  </h2>
                </b-card-title>

                <b-card-sub-title>
                  <h2>
                    <i>Sub - Labels: </i><b>{{ obj.subLabel }}</b>
                  </h2>
                </b-card-sub-title>

                <b-card-body>
                  <b-card-text> </b-card-text>
                  <b-button variant="primary" :id="obj.aId" v-on:click="setartist(obj.aId)" href="/#/artistpage"> PROFIL </b-button>
                  <b-button variant="success"> BUCHEN </b-button>
                </b-card-body>
              </b-card>
            </b-col>
          </div>
          <br />
        </div>
      </b-row>

      <br />
      <br />

      <b-row style="padding-bottom: 10px">
        <b-col>
          <b-card>
            <h1><b>RELEASING ARTISTS</b></h1>
          </b-card>
        </b-col>
      </b-row>

      <b-row>
        <div v-for="obj in arDat" v-bind:key="obj.aId">
          <div v-if="obj.type === 'r' && obj.online == 'True'">
            <b-col cols="12">
              <b-card img-top>
                <b-card-header>
                  <h2>
                    <b>{{ obj.name }}</b>
                  </h2>
                </b-card-header>

                <b-img
                  thumbnail
                  fluid
                  :src="obj.img"
                  width="313px"
                  height="300px"
                >
                </b-img>

                <b-card-title>
                  <h2>
                    <i>MAIN LABEL: </i><b>{{ obj.mainLabel }}</b>
                  </h2>
                </b-card-title>

                <b-card-sub-title>
                  <h2>
                    <i>Sub - Labels: </i><b>{{ obj.subLabel }}</b>
                  </h2>
                </b-card-sub-title>

                <b-card-body>
                  <b-card-text> </b-card-text>
                  <b-button variant="primary" :id="obj.aId" v-on:click="setartist(obj.aId)" href="/#/artistpage"> PROFIL </b-button>
                  <b-button variant="success" v-if="obj.showBooking == 'True'">
                    BUCHEN
                  </b-button>
                </b-card-body>
              </b-card>
            </b-col>
          </div>
          <br />
        </div>
      </b-row>

      <br />
      <br />

      <b-row style="padding-bottom: 10px">
        <b-col>
          <b-card>
            <h1><b>AGENGY ARTISTS</b></h1>
          </b-card>
        </b-col>
      </b-row>

      <b-row>
        <div v-for="obj in arDat" v-bind:key="obj.aId">
          <div v-if="obj.type === 'a' && obj.online == 'True'">
            <b-col cols="12">
              <b-card img-top>
                <b-card-header>
                  <h2>
                    <b>{{ obj.name }}</b>
                  </h2>
                </b-card-header>

                <b-img
                  thumbnail
                  fluid
                  :src="obj.img"
                  width="313px"
                  height="300px"
                >
                </b-img>

                <b-card-title>
                  <h2>
                    <i>MAIN LABEL: </i><b>{{ obj.mainLabel }}</b>
                  </h2>
                </b-card-title>

                <b-card-sub-title>
                  <h2>
                    <i>Sub - Labels: </i><b>{{ obj.subLabel }}</b>
                  </h2>
                </b-card-sub-title>

                <b-card-body>
                  <b-card-text> </b-card-text>
                  <b-button variant="primary" :id="obj.aId" v-on:click="setartist(obj.aId)" href="/#/artistpage"> PROFIL </b-button>
                  <b-button variant="success"> BUCHEN </b-button>
                </b-card-body>
              </b-card>
            </b-col>
          </div>
          <br />
        </div>
      </b-row>

      <br />
      <br />

      <b-row style="padding-bottom: 10px">
        <b-col>
          <b-card>
            <h1><b>COOPERATIONS</b></h1>
          </b-card>
        </b-col>
      </b-row>

      <b-row>
        <div v-for="obj in arDat" v-bind:key="obj.aId">
          <div v-if="obj.type === 'c' && obj.online == 'True'">
            <b-col cols="12">
              <b-card img-top>
                <b-card-header>
                  <h2>
                    <b>{{ obj.name }}</b>
                  </h2>
                </b-card-header>

                <b-img
                  thumbnail
                  fluid
                  :src="obj.img"
                  width="313px"
                  height="300px"
                >
                </b-img>

                <b-card-title>
                  <h2>
                    <i>MAIN LABEL: </i><b>{{ obj.mainLabel }}</b>
                  </h2>
                </b-card-title>

                <b-card-sub-title>
                  <h2>
                    <i>Sub - Labels: </i><b>{{ obj.subLabel }}</b>
                  </h2>
                </b-card-sub-title>

                <b-card-body>
                  <b-card-text> </b-card-text>
                  <b-button variant="primary" :id="obj.aId" v-on:click="setartist(obj.aId)" href="/#/artistpage"> PROFIL </b-button>
                  <b-button variant="success" v-if="obj.showBooking == 'True'">
                    BUCHEN
                  </b-button>
                </b-card-body>
              </b-card>
            </b-col>
          </div>
          <br />
        </div>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import arDat from "../db/json/a.json";
import globals from '../../global.utils';

export default {
  name: "Artists",
  data() {
    return {
      arDat,
    };
  },
  methods: {
    setartist: (id) => {
      console.log(id)
      var arrLen = Object.keys(arDat).length
      for(var i = 0; i < arrLen; i++){
        if(arDat[i].aId === id){
          globals.selectedArtist.name = arDat[i].name
          globals.selectedArtist.vita = arDat[i].vita
          globals.selectedArtist.references = arDat[i].references
          globals.selectedArtist.img = arDat[i].img
          globals.selectedArtist.soundcloud = arDat[i].soundcloud
          globals.selectedArtist.facebook = arDat[i].facebook
          globals.selectedArtist.youtube = arDat[i].youtube
          if(arDat[i].type == "c" || arDat[i].type == "r"){
            globals.selectedArtist.booking = false
          }
        }
      }      
    },
  },
};
</script>

<style scoped>
h1 > b {
  font-size: 24px !important;
  color: black !important;
}
h2 > b {
  font-size: 20px !important;
}
.artists{
  min-height: 100vh;
}
</style>


